<script setup lang="ts">
import { ref, computed } from 'vue'
import { Filter, Calendar, Eye } from 'lucide-vue-next'
import { useAuthStore } from '@/stores/auth'
import { useAttendanceHistory } from '@/composables/useAttendance'
import type { Attendance } from '@/types/attendance'
import EmployeeSelector from '@/components/attendance/EmployeeSelector.vue'

definePageMeta({
  layout: 'dashboard'
})

const route = useRoute()
const tenantSlug = route.params.tenant_slug as string
const authStore = useAuthStore()
const user = computed(() => authStore.user)

// Filters State
const selectedEmployeeId = ref('')
const startDateInput = ref('')
const endDateInput = ref('')
const statusInput = ref('')

const filters = ref({
    employeeId: '',
    startDate: '',
    endDate: '',
    status: ''
})

const handleApplyFilters = () => {
    filters.value = {
        employeeId: selectedEmployeeId.value,
        startDate: startDateInput.value,
        endDate: endDateInput.value,
        status: statusInput.value
    }
}

const handleResetFilters = () => {
    selectedEmployeeId.value = ''
    startDateInput.value = ''
    endDateInput.value = ''
    statusInput.value = ''
    
    filters.value = {
        employeeId: '',
        startDate: '',
        endDate: '',
        status: ''
    }
}

// Fetch Data
const { data: response, loading } = useAttendanceHistory(1, 10, filters.value)

// View Detail Modal
const viewAttendance = ref<Attendance | null>(null)

// Helpers
const getStatusBadge = (status: string | undefined) => {
    const badges: Record<string, string> = {
        PRESENT: 'bg-green-100 text-green-800',
        LATE: 'bg-yellow-100 text-yellow-800',
        ABSENT: 'bg-red-100 text-red-800',
        EXCUSED: 'bg-blue-100 text-blue-800',
    }
    return badges[status || ''] || 'bg-gray-100 text-gray-800'
}

const formatTime = (dateString: string | null) => {
    if (!dateString) return '-'
    return new Date(dateString).toLocaleTimeString('en-US', { 
        hour: '2-digit', 
        minute: '2-digit',
        hour12: false
    })
}

const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', { 
        year: 'numeric', month: 'long', day: 'numeric' 
    })
}
</script>

<template>
    <div class="space-y-6">
      <div class="flex justify-between items-center">
        <h2 class="text-2xl font-bold text-gray-900">Attendance History</h2>
        <NuxtLink :to="tenantSlug ? `/${tenantSlug}/dashboard/attendance` : '/dashboard/attendance'">
          <UiButton variant="secondary">Back to Attendance</UiButton>
        </NuxtLink>
      </div>

      <!-- Filters -->
      <UiCard>
        <UiCardContent class="pt-6">
          <div class="space-y-4">
            <!-- Employee Selector for HR/SUPER/ADMIN -->
            <EmployeeSelector
                 v-if="user?.role === 'HR' || user?.role === 'SUPER' || user?.role === 'ADMIN'"
                 v-model="selectedEmployeeId"
            />

            <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              <UiInput
                label="Start Date"
                type="date"
                v-model="startDateInput"
                class="bg-white border-gray-600"
              />
              <UiInput
                label="End Date"
                type="date"
                v-model="endDateInput"
                 class="bg-white border-gray-600"
              />
              <div>
                <label class="block text-sm font-bold text-gray-900 mb-2">Status</label>
                <div class="relative">
                  <select
                    v-model="statusInput"
                    class="w-full appearance-none px-4 py-2 pr-10 border border-gray-600 rounded-md focus:outline-none focus:ring-brand-cyan focus:border-brand-cyan bg-white text-black cursor-pointer"
                  >
                    <option value="">All Status</option>
                    <option value="PRESENT">Present</option>
                    <option value="LATE">Late</option>
                    <option value="ABSENT">Absent</option>
                    <option value="EXCUSED">Excused</option>
                  </select>
                   <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                        <svg class="h-4 w-4 fill-current" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
                            <path d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" />
                        </svg>
                    </div>
                </div>
              </div>
              <div class="flex items-end gap-2">
                <UiButton @click="handleApplyFilters" class="flex-1">
                  <Filter class="w-4 h-4 mr-2" />
                  Apply
                </UiButton>
                <UiButton variant="secondary" @click="handleResetFilters" class="flex-1">
                  Reset
                </UiButton>
              </div>
            </div>
          </div>
        </UiCardContent>
      </UiCard>

      <!-- History Table -->
      <UiCard>
        <UiCardHeader>
          <UiCardTitle>Attendance Records</UiCardTitle>
        </UiCardHeader>
        <UiCardContent class="overflow-x-auto">
          <div v-if="loading" class="flex flex-col items-center justify-center py-12">
            <div class="animate-spin rounded-full h-12 w-12 border-b-2 border-brand-navy mb-4"></div>
            <p class="text-gray-600">Loading attendance history...</p>
          </div>
          
          <table v-else class="min-w-full divide-y divide-gray-200">
            <thead class="bg-gray-50">
              <tr>
                <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Date</th>
                <th v-if="user?.role === 'HR' || user?.role === 'SUPER' || user?.role === 'ADMIN'" class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Employee</th>
                <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Clock In</th>
                <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Clock Out</th>
                <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Total Hours</th>
                <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Notes</th>
                <th class="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
              </tr>
            </thead>
            <tbody class="bg-white divide-y divide-gray-200">
                <tr v-if="!response?.data || response.data.length === 0">
                     <td colspan="8" class="px-6 py-8 text-center text-gray-500">
                      No attendance records found
                    </td>
                </tr>
                <tr v-for="attendance in response?.data" :key="attendance.id" class="hover:bg-gray-50">
                    <td class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <div class="flex items-center">
                        <Calendar class="w-4 h-4 mr-2 text-gray-400" />
                        {{ new Date(attendance.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' }) }}
                      </div>
                    </td>
                     <td v-if="user?.role === 'HR' || user?.role === 'SUPER' || user?.role === 'ADMIN'" class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {{ attendance.employee?.firstName }} {{ attendance.employee?.lastName }}
                    </td>
                    <td class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {{ formatTime(attendance.checkIn) }}
                    </td>
                    <td class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {{ formatTime(attendance.checkOut) }}
                    </td>
                    <td class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {{ attendance.workDuration ? `${(attendance.workDuration / 60).toFixed(1)}h` : '-' }}
                    </td>
                    <td class="px-6 py-4 whitespace-nowrap">
                      <span class="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium" :class="getStatusBadge(attendance.status)">
                        {{ attendance.status }}
                      </span>
                    </td>
                    <td class="px-6 py-4 text-sm text-gray-600 max-w-xs truncate">
                      {{ attendance.notes || '-' }}
                    </td>
                    <td class="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <UiButton 
                        variant="secondary" 
                        class="p-2" 
                        @click="viewAttendance = attendance"
                        title="View Details"
                      >
                        <Eye class="w-4 h-4" />
                      </UiButton>
                    </td>
                </tr>
            </tbody>
          </table>
        </UiCardContent>
      </UiCard>

      <!-- View Detail Modal -->
      <div v-if="viewAttendance" class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
        <div class="bg-white rounded-lg max-w-4xl w-full max-h-[90vh] overflow-y-auto">
          <div class="sticky top-0 bg-white border-b border-gray-200 px-6 py-4 flex items-center justify-between">
            <h3 class="text-xl font-bold text-gray-900">Attendance Details</h3>
            <button
              @click="viewAttendance = null"
              class="p-2 hover:bg-gray-100 rounded-full transition-colors"
            >
              <svg class="w-5 h-5 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
          <div class="p-6 space-y-6">
            <!-- Employee Info -->
            <div v-if="viewAttendance.employee" class="bg-gray-50 p-4 rounded-lg">
              <h4 class="font-bold text-gray-900 mb-2">Employee Information</h4>
              <div class="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                <div>
                  <p class="text-gray-600">Name</p>
                  <p class="font-medium text-gray-900">
                    {{ viewAttendance.employee.firstName }} {{ viewAttendance.employee.lastName }}
                  </p>
                </div>
                <div>
                  <p class="text-gray-600">Email</p>
                  <p class="font-medium text-gray-900">{{ viewAttendance.employee.user.email }}</p>
                </div>
                <div>
                  <p class="text-gray-600">Position</p>
                  <p class="font-medium text-gray-900">{{ viewAttendance.employee.position }}</p>
                </div>
                <div>
                  <p class="text-gray-600">Department</p>
                  <p class="font-medium text-gray-900">{{ viewAttendance.employee.department }}</p>
                </div>
              </div>
            </div>

            <!-- Attendance Info -->
            <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label class="block text-sm font-bold text-gray-700 mb-1">Date</label>
                <p class="text-gray-900">
                  {{ formatDate(viewAttendance.date) }}
                </p>
              </div>
              <div>
                <label class="block text-sm font-bold text-gray-700 mb-1">Status</label>
                <span class="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium" :class="getStatusBadge(viewAttendance.status)">
                  {{ viewAttendance.status }}
                </span>
              </div>
              <div>
                <label class="block text-sm font-bold text-gray-700 mb-1">Check In</label>
                <p class="text-gray-900">{{ formatTime(viewAttendance.checkIn) }}</p>
              </div>
              <div>
                <label class="block text-sm font-bold text-gray-700 mb-1">Check Out</label>
                <p class="text-gray-900">{{ formatTime(viewAttendance.checkOut) }}</p>
              </div>
              <div>
                <label class="block text-sm font-bold text-gray-700 mb-1">Work Duration</label>
                <p class="text-gray-900">
                  {{ viewAttendance.workDuration ? `${(viewAttendance.workDuration / 60).toFixed(1)} hours` : '-' }}
                </p>
              </div>
              <div>
                <label class="block text-sm font-bold text-gray-700 mb-1">Period</label>
                <p class="text-gray-900">{{ viewAttendance.attendancePeriod?.name || '-' }}</p>
              </div>
              <div v-if="viewAttendance.notes" class="md:col-span-2">
                <label class="block text-sm font-bold text-gray-700 mb-1">Notes</label>
                <p class="text-gray-900">{{ viewAttendance.notes }}</p>
              </div>
            </div>

             <!-- Map placeholder -->
             <div v-if="viewAttendance.checkInLocation || viewAttendance.checkOutLocation" class="bg-gray-50 border border-gray-200 rounded p-4 text-center">
                <p class="text-sm text-gray-500">Map view is not available yet.</p>
             </div>
          </div>
          <div class="sticky bottom-0 bg-gray-50 px-6 py-4 border-t border-gray-200">
            <UiButton @click="viewAttendance = null" variant="secondary" class="w-full">
              Close
            </UiButton>
          </div>
        </div>
      </div>
    </div>
</template>
